import React, { useEffect } from "react";

const { kakao } = window;

export default function KakaoMap() {
  useEffect(() => {
    const container = document.getElementById("map");
    const options = {
      center: new kakao.maps.LatLng(37.577411, 126.984271),
      level: 3,
    };
    const map = new kakao.maps.Map(container, options);

    //   
    const markerPosition = new kakao.maps.LatLng(37.577411, 126.984271);

    //  
    const marker = new kakao.maps.Marker({
      position: markerPosition,
    });

    //     
    marker.setMap(map);

    const iwContent =
        '<div class="w-full"><div class="text-black bg-pink-100 text-center font-semibold text-sm w-[180px]"> Handus<br><a href="https://map.kakao.com/link/map/Handus_Sean&Sally,37.577411, 126.984271" class="bg-pink-200 text-xs" style="color:black" target="_blank"></a> <a href="https://map.kakao.com/link/to/Handus_Sean&Sally,37.577411, 126.984271" class="bg-pink-200 text-xs" style="color:black" target="_blank"></a></div></div>',
      //    HTML  document element 
      iwPosition = new kakao.maps.LatLng(37.577411, 126.984271); //  

    //  
    const infowindow = new kakao.maps.InfoWindow({
      position: iwPosition,
      content: iwContent,
    });

    //    .   marker     
    infowindow.open(map, marker);
  }, []);
  return (
    <div id="map" className="w-[360px] h-[220px] rounded-md mx-auto"></div>
  );
}
